//import Jump from './Jump'
//import './index.html'

if (window.self === window.top)
   window.location = 'host/index.html#/'


const emit = (x, y) => {
  window.parent.postMessage({ type:x, payload:y }, '*')
}

export const start = () => emit('start')
export const exit = () => emit('exit')
export const load = x => {

  if (!x.url) throw 'url required.'

  const game = UnityLoader.instantiate("game", x.url, {
    onProgress: (x, y) => emit('progress', y)
  })

  window.addEventListener('message', x => {

    x = x.data

    switch(x.type) {

      case 'init':
        game.SendMessage('JumpPlugin', 'Init', x.payload.userId)
        break

      case 'fullscreen':
        game.SetFullscreen(1)
        break;

    }

  })

}



